// MODAL CLOSE BUTTON
.close {
    position: absolute;
    z-index: 50;
    top: -85%;
    right: 5%;
    background: rgba(255, 255, 255, .3);
    color: white;
    padding: .5rem .5rem .3rem .5rem;
    outline: none;
    border: none;
    border-radius: 3px;
    font-size: 1.1rem;
        &:focus {
            background: radial-gradient(rgba(0, 0, 0, .1), rgba(0, 0, 0, .9));
        }
        &:hover {
            cursor: pointer;
        }
}

// IMAGE
.dimensions {
    height: 100%;
    width: 100%;
    object-fit: contain;
}

// PHOTO INFORMATION POPUP STYLES
.information,
.hidden {
    position: absolute;
    border-radius: 3px;
    width: 80%;
    left: 50%;
    top: -55%;
    transform: translate(-50%);
    color: white;
    align-self: start;
    background: radial-gradient(rgba(0, 0, 0, .5), rgba(0, 0, 0, .9));
    padding-top: 1.5rem;
    padding-bottom: 1rem;
    padding: 1.5rem 1.5rem 1rem 1.5rem;
}

.information {
    opacity: 1;
    transition-duration: .3s;
    transition-property: opacity;
}

.hidden {
    opacity: 0;
    transition-duration: .7s;
    transition-property: opacity;
}

// FORWARD AND BACK BUTTON STYLES
.mobileButton,
.buttonForward,
.buttonBack {
    color: white;
    display: flex;
    border-radius: 3px;
    border: none;
    background: transparent;
}

.mobileButton {
    animation-duration: 1s;
    animation-iteration-count: 1;
}

