.buttonForward,
.buttonBack {
    transition: .3s;
    &:hover {
        background: rgba(16, 16, 16, 0.776);
        cursor: pointer;
    }
    &:focus {
        background: rgba(16, 16, 16, 1);
        outline: none;
    }
}

.back,
.forward {
    position: absolute;
    z-index: 1;
    top: 110%;
    
}

.back {
    left: 30%;
}

.forward {
    right: 30%;
}


// DISABLE CYCLE BUTTON
.disable {
    color: rgba(0, 0, 0, 0);
}

@media screen and (min-width: 430px) {
    .information {
        top: -45%;
    }

    .close {
        top: -75%;
    }
}

@media screen and (min-width: 530px) {
    .information {
        top: -35%;
    }

    .close {
        top: -55%;
    }
}

// LANDSCAPE MOBILE STYLES
@media screen and (orientation: landscape) {
    .back,
    .forward {
        position: relative;
        top: 0;
    }

    .back {
        left: 0;
        margin-right: 1rem;
        padding: .7rem .5rem .7rem .2rem;  
    }

    .forward {
        right: 0;
        margin-left: 1rem;
        padding: .7rem .2rem .7rem .5rem;
    }

    .close {
        top: 2.5%; 
        right: 1%;
    }

    .information {
        top: 5%;
        left: 30%;
        width: 50%;
        // margin-top: 25px;
    }
}

